<template>
    <div>
         <div v-if="imageload" style="position: absolute;top:0px;z-index:1000;">
            <div class="bg-dark text-white" style="width:100vw;">
                <div class="row ">
                    <div class="col-1 bg-blue-400 text-center animated slideInLeft" style="height:25px;" v-for="(item, index) in loadnya" :key="index+'loadnya'">
                        <span v-if="index==0">
                            Loading
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="modal">
            <div style="height:100vh;position:fixed;top:0;left:0;width:100vw;" class="d-flex justify-content-center align-items-center">
                <div style="width:100%">
                    <div class="row justify-content-center">
                      <div class="col-md-9">
                          <div class="bg-white p-3 rounded-lg shadow">
                              Lorem, ipsum dolor sit amet consectetur adipisicing elit. Facilis, aperiam? Ipsum corrupti laudantium voluptate dolor rem, veritatis eligendi dolores eum explicabo quidem maxime odit id harum nulla ad minima vel.
                          </div>
                      </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="rightSide" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:100;"
        class=""
        >
            <div class="row justify-content-center">
                <div class="col-md-12">
                    <div class="bg-white animated fadeInDown p-3 rounded shadow">
                        <button type="button" class="btn btn-sm btn-danger  float-right" @click="rightSide=false">x</button>
                        <br>
                        <hr class="mt-1 mb-1">
                        <p class="text-center" v-if="dataSide.type=='Text'">
                            {{dataSide.text}}
                        </p>
                        <div
                         v-if="dataSide.type=='Photo'" 
                        >
                            <img :src="dataSide.gambar"
                            v-viewer
                            style="height:300px;"
                            alt="">
                            <hr class="mt-2 mb-2">
                            <p class="text-justify" v-html="dataSide.detail"></p>
                        </div>
                         <div
                         v-if="dataSide.type=='Video'" 
                        >
                            <iframe style="height:300px;width:450px;" :src="dataSide.youtube" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                             <hr class="mt-2 mb-2">
                            <p class="text-justify" v-html="dataSide.detail"></p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="ready">
            <div id="container" style="height:100vh;width:100vw;"></div>
        </div>
        <div style="position:fixed;z-index:10000;bottom:0px;left:0px;width:100vw;" :style="showGalery?'height:150px':'height:30px;'">
            <div style="height:30px;width:100vw;">
                <div class="row justify-content-center">
                  <div class="col-md-6 text-center">
                    <button type="button" class="btn btn-sm btn-dark p-1 " @click="showGalery=!showGalery">
                        <img src="https://upload.egov.balangankab.go.id/svg/image.svg" 
                         class="d-inline" style="height:20px;width:20px;filter: brightness(0) invert(1);" alt="">
                         <img src="https://upload.egov.balangankab.go.id/svg/arrow-up.svg" 
                         class="d-inline ml-3" style="height:20px;width:20px;filter: brightness(0) invert(1);" alt="">
                         </button>
                  </div>
                </div>
            </div>
            <div 
            v-if="showGalery"
            style="background: rgb(39,45,26);
background: linear-gradient(0deg, rgba(39,45,26,0.3958625686602766) 0%, rgba(40,42,23,0.37625472552302175) 100%);" class="p-2 rounded-t-lg shadow">
                <carousel :scrollPerPage="false" :per-page="6" :autoplay="true" :autoplayTimeout="2000" :speed="1000" :loop="true" :paginationEnabled="false">
                    <slide v-for="(item, index) in obj" :key="index+'imageslide'" style="height:100px;">
                        <div style="height:100px;" class="d-flex justify-content-center align-items-center ">
                            <div style="width:100%">
                                <img :src="item.gambar2" 
                                @click="showPano(index)"
                                style="display:block;margin:auto;height:100px;width:130px;" 
                                class="cursor-pointer"
                                alt="">
                            </div>
                        </div>
                    </slide>
                </carousel>
            </div>
        </div>
    </div>
</template>
<script>
import autophp from "@/plugins/autophp_laravel.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let urlnya=localStorage.getItem('urlnya');
let sdb = new autophp();
import { Carousel, Slide } from 'vue-carousel';
var obj=[];
var viewer;
export default{
    components:{
        Btables,
        Carousel,
        Slide
    },
    data(){
        return{
            photo:[],
            listphoto:[],
            listHotspot:[],
            obj:[],
            imageload:true,
            loadnya:[],
            modal:false,
            show:false,
            ready:false,
            showGalery:false,
            rightSide:false,
            gambarUtama:'',
            btn:'tambah',
            vdata:{},
            info:{},
            dataSide:{},
            point:null
        }
    },
    methods: {
        getDatanya(){
            this.ready=false;
            sdb.collection("app_gis_360data_hotspot",false,urlnya).doc().select(`select * from app_gis_360data_hotspot`).then(res => {
                this.datanya=res;
                this.ready=true;
                this.$forceUpdate();
            });
        },
        getData(){
            sdb.collection("app_gis_360data",false,urlnya).doc().select(`select * from app_gis_360data where id='${this.$route.query.idgambar}'`).then(res => {
                this.photo=res;
                this.$forceUpdate();
            });
        },
        getListPhoto(){
            let that=this;
            sdb.collection("app_gis_360data",false,urlnya).doc().select(`select * from app_gis_360data`).then(res => {
                this.listphoto=res;
                 sdb.collection("app_gis_360data_hotspot",false,urlnya).doc().select(`select * from app_gis_360data_hotspot`).then(res => {
                    that.listHotspot=res;
                    let obj=[];
                    that.listphoto.forEach((e,i)=>{
                        obj[i]={
                            ...e,
                            hotspot:that.listHotspot.filter(k=>{
                                return k.idgambar==e.id
                            })
                        }
                    })
                    that.$forceUpdate();
                });
            });
        },
        getListHotspot(){
           
        },
        showPano(i){
            viewer.setPanorama(this.obj[i].panorama);
        }
    },
    mounted() {
        let that=this;

        this.getData();
        this.getDatanya();
         sdb.collection("app_gis_360data",false,urlnya).doc().select(`select * from app_gis_360data`).then(res => {
                this.listphoto=res;
                 sdb.collection("app_gis_360data_hotspot",false,urlnya).doc().select(`select tb1.type,tb1.initialRoute,tb1.icon,tb1.detail,tb1.text,tb1.gambar,tb1.idgambar,tb1.youtube,tb1.point,tb1.hotspot,tb1.idroute,tb1.idpeta,tb2.gambar as gambarnya ,tb1.iconnya,tb1.title,tb1.size
                 from app_gis_360data_hotspot tb1 left join app_gis_360data tb2 on tb2.id=tb1.idgambar`).then(res => {
                    that.listHotspot=res;
                    that.listphoto.forEach((e,i)=>{
                        that.obj[i]={
                            ...e,
                            hotspot:that.listHotspot.filter(k=>{
                                return k.idgambar==e.id
                            })
                        }
                    })
                    var pertama=0;
                    that.obj.forEach((e,i)=>{
                        if(e.id==that.$route.query.idgambar){
                            pertama=i;
                        }
                    })
                    var temp=that.obj[pertama]
                    that.obj.splice(pertama, 1);
                    that.obj.unshift(temp);
                    var container;
                    var infospots=[];
                    container = document.querySelector( '#container' );
                    viewer = new PANOLENS.Viewer( { 
                        container: container,
                        cameraFov: 60,
                        reverseDragging: false, 
                        autoHideInfospot:false 
                        } );
                    that.obj.forEach((e,i)=>{
                       
                        that.obj[i].panorama=new PANOLENS.ImagePanorama(e.gambar);
                         that.obj[i].panorama.addEventListener('progress', function(e){
                            that.imageload=true;
                            that.loadnya.push('1')
                            that.$forceUpdate();
                            console.log('image progress')
                        });
                        that.obj[i].panorama.addEventListener('load', function(e){
                            that.imageload=false;
                            that.loadnya=[]
                            that.$forceUpdate();
                        });
                    })
                    that.listHotspot.forEach((e,i)=>{
                        console.log('icn',e.icon)
                        if(e.icon=='Custom'){
                            // getBase64FromUrl(e.iconnya).then(dataUrl=>{
                                    infospots[i] = new PANOLENS.Infospot( parseInt(e.size), e.iconnya );
                                    infospots[i].position.set( parseFloat(JSON.parse(e.hotspot).x), parseFloat(JSON.parse(e.hotspot).y), parseFloat(JSON.parse(e.hotspot).z) );
                                    infospots[i].addHoverText( e.title );
                                    infospots[i].addEventListener( 'click', function(){
                                        if(e.type=="Text"){
                                            that.rightSide=!that.rightSide;
                                            that.dataSide=e;
                                            that.$forceUpdate();
                                        }else if(e.type=="Photo"){
                                            that.rightSide=!that.rightSide;
                                            that.dataSide=e;
                                            that.$forceUpdate();
                                        }else if(e.type=="Video"){
                                            that.rightSide=!that.rightSide;
                                            that.dataSide=e;
                                            that.$forceUpdate();
                                        }else if(e.type=="Route"){
                                            viewer.setPanorama(that.obj.filter(k=>k.id==e.idroute)[0].panorama);
                                            setTimeout(() => {
                                                viewer.tweenControlCenter( new THREE.Vector3( parseFloat(JSON.parse(e.initialRoute).x), parseFloat(JSON.parse(e.initialRoute).y), parseFloat(JSON.parse(e.initialRoute).z)), 0 );
                                            }, 200);
                                        }
                                    });
                                    that.obj.filter(k=>k.id==e.idgambar)[0].panorama.add(infospots[i]);
                                // })
                        }else{
                            if(e.icon=="Arrow"){
                                infospots[i] = new PANOLENS.Infospot( parseInt(e.size), PANOLENS.DataImage.Arrow );
                                // infospots[i] = new PANOLENS.Infospot( parseInt(e.size), PANOLENS.DataImage.Info );
                            }else if(e.icon=="Info"){
                                infospots[i] = new PANOLENS.Infospot( parseInt(e.size), PANOLENS.DataImage.Info );
                            }
                            infospots[i].position.set( parseFloat(JSON.parse(e.hotspot).x), parseFloat(JSON.parse(e.hotspot).y), parseFloat(JSON.parse(e.hotspot).z) );
                            infospots[i].addHoverText( e.title );
                            infospots[i].addEventListener( 'click', function(){
                                if(e.type=="Text"){
                                    that.rightSide=!that.rightSide;
                                    that.dataSide=e;
                                    that.$forceUpdate();
                                }else if(e.type=="Photo"){
                                    that.rightSide=!that.rightSide;
                                    that.dataSide=e;
                                    that.$forceUpdate();
                                }else if(e.type=="Video"){
                                    that.rightSide=!that.rightSide;
                                    that.dataSide=e;
                                    that.$forceUpdate();
                                }else if(e.type=="Route"){
                                    viewer.setPanorama(that.obj.filter(k=>k.id==e.idroute)[0].panorama);
                                    setTimeout(() => {
                                        viewer.tweenControlCenter( new THREE.Vector3( parseFloat(JSON.parse(e.initialRoute).x), parseFloat(JSON.parse(e.initialRoute).y), parseFloat(JSON.parse(e.initialRoute).z)), 0 );
                                    }, 200);
                                }
                            });
                            that.obj.filter(k=>k.id==e.idgambar)[0].panorama.add(infospots[i]);
                        }
                    })
                    setTimeout(() => {
                        that.obj.forEach((e,i)=>{
                            viewer.add(e.panorama);
                        })
                    }, 500);
                    // setTimeout(() => {
                    //     document.addEventListener('contextmenu', event => {
                    //         alert('Right Click Disabled');    
                    //         event.preventDefault()
                    //     });
                    // }, 3000);
                    viewer.addUpdateCallback(function(){
                    });
                    that.$forceUpdate();
                });
            });
            const getBase64FromUrl = async (url) => {
                const data = await fetch(url);
                const blob = await data.blob();
                return new Promise((resolve) => {
                    const reader = new FileReader();
                    reader.readAsDataURL(blob); 
                    reader.onloadend = () => {
                    const base64data = reader.result;   
                    resolve(base64data);
                    }
                });
            }
             
    },
    // metaInfo: {
    //     script: [
    //         {
    //             src: "https://gis.egov.balangankab.go.id/files/three.min.js"
    //         },
    //         {
    //             src: "https://gis.egov.balangankab.go.id/files/panolens.js"
    //         },
    //     ]
    // }
}
</script>